body{
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
}

.square{
  border: 1px solid red;
  float: left;
  font-size: 40px;
  height: 100px;
  padding: 0px;
  text-align: center;
  width: 100px;
  margin-top: -1px;
  margin-left: -1px ;
  cursor: pointer;
}
.row{
  display: flex;
}